import tkiQrcode from "./tki-qrcode"
export default {
    props: {
        boxInfo: {
            type: Object,
            require: true
        },
        showCheckinBox: {
            type: String,
            default: false
        },
    },    
    data() {
        return {
            currentTime: '',
            timer: null,
            QRStr: '',
            isChange: false,
            src: ''
        }
    },
    watch: {
        currentTime() {                  
            this.changeQR()
        }
    },
    created() {        
        this.creatQrcode()
        this.openTimer()
    },
    destroyed() {        
        clearInterval(this.timer)
    },
    components: {tkiQrcode},
    methods: {
        closeBox () {
            this.$emit('closeBox')
        },
        openTimer() {                
            //定时刷新
            this.timer = setInterval(() => {
                this.currentTime = new Date().getTime()
            },120000)
        },
        qrR(res) {
            this.src = res
        },  
        clickImg: function(){
            var imgUrl = this.boxInfo.bindingPhoto;
            uni.previewImage({
                urls: [imgUrl], //需要预览的图片http链接列表，注意是数组
                success: function (res) { },
                fail: function (res) { },
                complete: function (res) { },
            })
        },
        creatQrcode() {
            let temp = new Date()
            this.currentTime = new Date().getTime()
            this.$nextTick(() => {                    
                this.$refs.qrcode._makeCode()
            })
        },        
        handleChangeQR () {
            if (this.isChange) {
                return
            }                
            this.isChange = true
            setTimeout(() => {
                this.isChange = false
            },2000)
            this.currentTime = new Date().getTime()
        },
        changeQR () {   
            if (this.QRStr) {
                this.$refs.qrcode._clearCode()         
            }
            let code = this.boxInfo.childCode
            let pcode = this.boxInfo.password
            let un = this.boxInfo.username
            let t = this.currentTime
            let obj = {
                code,pcode,un,t
            }
            this.QRStr = JSON.stringify(obj)
        },
    },
}
